import axios from "@/axios.js"

export default {
  fetchBooks({ commit }) {
    return new Promise((resolve, reject) => {
      axios.get("/admin/books")
        .then((response) => {
          commit('SET_BOOKS', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  fetchBook(context, bookId) {
    return new Promise((resolve, reject) => {
      axios.get(`/admin/books/${bookId}`)
        .then((response) => {
          resolve(this.state.responseDataPath ? response[this.state.responseDataPath] : response)
        })
        .catch((error) => { reject(error) })
    })
  },
  addBook({ commit }, data) {
    return new Promise((resolve, reject) => {
      const attributes = {...data, status: data.status.value }
      axios.post("/admin/books", { attributes })
        .then((response) => {
          commit('ADD_BOOK', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  updateBook({ commit }, data) {
    return new Promise((resolve, reject) => {

      // const attributes = {...data, status: data.status.value }
      const attributes = {...data, status: data.status.value }

      axios.patch(`/admin/books/${data.id}`, {
        attributes
      })
        .then((response) => {
          console.log(data)
          console.log(response)
          commit('UPDATE_BOOK', Object.assign(data, {id: response.data.data.id}))
          resolve(response)
        })
        .catch((error) => {
          console.log('error')
          console.log(error)
          reject(error) })
    })
  },
  removeRecord({ commit }, bookId) {
    return new Promise((resolve, reject) => {
      axios.delete(`/admin/books/${bookId}`)
        .then((response) => {
          commit('REMOVE_BOOK', bookId)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  }
}
