<template>
  <div id="user-edit-tab-info">

    <!-- Content Row -->
    <div class="vx-row">
      <div class="vx-col md:w-1/2 w-full">
        <vs-input class="w-full mt-4" :label="$t('book.title')" v-model="dataLocal.title" v-validate="'required'" name="title" />
        <span class="text-danger text-sm"  v-show="errors.has('title')">{{ errors.first('title') }}</span>

        <vs-input class="w-full mt-4" :label="$t('book.author')" v-model="dataLocal.author" v-validate="'required'" name="author" />
        <span class="text-danger text-sm"  v-show="errors.has('author')">{{ errors.first('author') }}</span>

        <vs-input class="w-full mt-4" :label="$t('book.publishingHouse')" v-model="dataLocal.publishingHouse" v-validate="'required'" name="publishingHouse" />
        <span class="text-danger text-sm"  v-show="errors.has('publishingHouse')">{{ errors.first('publishingHouse') }}</span>
      </div>
      <div class="vx-col md:w-1/2 w-full">
        <vs-input class="w-full mt-4" :label="$t('book.isbn')" v-model="dataLocal.isbn" v-validate="'required'" name="isbn" />
        <span class="text-danger text-sm"  v-show="errors.has('isbn')">{{ errors.first('isbn') }}</span>

        <vs-input class="w-full mt-4" :label="$t('book.price')" v-model="dataLocal.price" v-validate="'required'" name="price" />
        <span class="text-danger text-sm"  v-show="errors.has('price')">{{ errors.first('price') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">{{ this.$t('book.status') }}</label>
          <v-select v-model="dataLocal.status" :clearable="false" :options="statusOptions" v-validate="'required'" name="status" :dir="$vs.rtl ? 'rtl' : 'ltr'" />
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('status')">{{ errors.first('status') }}</span>
      </div>
    </div>

    <!-- Save & Reset Button -->
    <div class="vx-row">
      <div class="vx-col w-full">
        <div class="mt-8 flex flex-wrap items-center justify-end">
          <vs-button class="ml-auto mt-2" @click="save_changes" :disabled="!validateForm">{{ $t('button.save') }}</vs-button>
          <vs-button class="ml-4 mt-2" type="border" color="warning" @click="reset_data">{{ $t('button.reset') }}</vs-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import vSelect from 'vue-select'
import { mapActions } from 'vuex'

export default {
  components: {
    vSelect
  },
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      loading: false,
      dataLocal: JSON.parse(JSON.stringify(this.data)),
      statusOptions: [
        { label: this.$t('active'),  value: 1 },
        { label: this.$t('deactivated'),  value: 0 },
      ],
    }
  },
  computed: {
    validateForm() {
      return !this.errors.any()
    }
  },
  methods: {
    ...mapActions(['setAlert']),
    save_changes() {
      if(!this.validateForm) return

      this.$vs.loading()

      this.$store.dispatch("bookManagement/updateBook", this.dataLocal)
      .then(res => {
        this.book_data = res.data.attributes
        this.setAlert({
          type: 'success',
          message: 'alert.edit.success'
        })
        this.$router.push({ name: 'admin-book-list' })
      })
      .catch(err => {
        console.error(err)
      })
      .then(() => {
        this.$vs.loading.close()
      })

      // Here will go your API call for updating data
      // You can get data in "this.dataLocal"

    },
    reset_data() {
      this.dataLocal = JSON.parse(JSON.stringify(this.data))
    }
  },
}
</script>
