<template>
  <div id="page-book-edit">

    <vs-alert color="danger" title="Book Not Found" :active.sync="book_not_found">
      <span>{{ $t('book.notFound.message', { bookId: $route.params.bookId }) }} </span>
    </vs-alert>

    <vx-card v-if="book_data">

      <div slot="no-body" class="tabs-container px-6 pt-6">

        <vs-tabs v-model="activeTab" class="tab-action-btn-fill-conatiner">
          <vs-tab :label="$t('book.book')" icon-pack="feather" icon="icon-book">
            <div class="tab-text">
              <book-edit-tab-account class="mt-4" :data="book_data" />
            </div>
          </vs-tab>
        </vs-tabs>

      </div>

    </vx-card>
  </div>
</template>

<script>
import BookEditTabAccount from "./BookEditTabAccount.vue"

// Store Module
import moduleBookManagement from '@/store/book-management/moduleBookManagement.js'

export default {
  components: {
    BookEditTabAccount,
  },
  data() {
    return {
      book_data: null,
      book_not_found: false,

      /*
        This property is created for fetching latest data from API when tab is changed

        Please check it's watcher
      */
      activeTab: 0,
    }
  },
  watch: {
    activeTab() {
      this.fetch_book_data(this.$route.params.bookId)
    }
  },
  methods: {
    fetch_book_data(bookId) {
      this.$store.dispatch("bookManagement/fetchBook", bookId)
        .then(res => {
          this.book_data = { ...res.data.attributes, id: res.data.id }
        })
        .catch(err => {
          if(err.response.status === 404) {
            this.book_not_found = true
            return
          }
          console.error(err) })
    }
  },
  created() {
    // Register Module BookManagement Module
    if(!moduleBookManagement.isRegistered) {
      this.$store.registerModule('bookManagement', moduleBookManagement)
      moduleBookManagement.isRegistered = true
    }
    this.fetch_book_data(this.$route.params.bookId)
  }
}

</script>
